/* eslint-disable max-classes-per-file */
export interface MemberModel {
  openEdit: boolean;
  confirmed: boolean;
  uuid: string;
  name: string;
  phone: string;
}

export interface MemberGroupPreview {
  uuid: string;
  name: string;
  uniqueName: string;
  memberCount: number;
  iconChecked: boolean;
}

export interface MemberGroup {
  uuid: string;
  name: string;
  uniqueName: string;
  members: Member[];
}

export class Member implements MemberModel {
  uuid: string;

  name: string;

  phone: string;

  openEdit = false;

  confirmed: boolean;

  constructor(uuid: string, name: string, phone: string, confirmed: boolean) {
    this.uuid = uuid;
    this.name = name;
    this.phone = phone;
    this.confirmed = confirmed;
  }
}

export class UpdateMemberPayload {
  confirmed: boolean;

  constructor(confirmed: boolean) {
    this.confirmed = confirmed;
  }
}

export class UpdateMemberGroupPayload {
  name: string;

  constructor(name: string) {
    this.name = name;
  }
}
