    };
  },
  computed: {
    previewImage(): string {
      if (this.defaultThumbnial) {
        return `${this.defaultThumbnial}/240`;
      }
      return this.defaultThumbnial;
    },
    dialogId(): string {
      return this.group.uuid;
    },
  },
  mounted() {
    // MessageEventBus.$on('clear-copied-message', this.clearCopyMessageTimeout);
