  methods: {
    enterGroup() {
      this.$store.commit('changeMemberGroup', this.group);
      this.$router.push('/manager/membergroup-maker');
    },
    removeGroup() {
      this.openRemoveModal();
    },
    openRemoveModal() {
      this.$modal.show('dialog', {
        title: '確定刪除嗎?',
        text: '這個群組刪掉就不會再回來了',
        buttons: [
          {
            title: '取消',
            handler: () => {
              this.$modal.hide('dialog');
            },
          },
          {
            title: '確定',
            handler: () => {
              // remove operation
              this.$modal.hide('dialog');
              this.$emit('removeMe');
            },
          },
        ],
      });
    },
